.song-container {
	min-height: 60vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.song-container img {
	width: 20%;
	border-radius: 50%;
}

.song-container h2 {
	padding: 3rem 1rem 1rem 1rem;
}

.song-container h3 {
	font-size: 1rem;
}
@media screen and (max-width:768px){
	.song-container img{
		width: 60%
	}
}